:root {
  --primary-gradient: linear-gradient(90deg, #00f5a0 0%, #00d9f5 100%);
  --secondary-gradient: linear-gradient(90deg, #1a1a2e 0%, #16213e 100%);
  --accent-color: #00f5a0;
  --background-dark: #0f0f1e;
  --text-light: #ffffff;
  --text-dim: #8a8f98;
}

.App {
  background-color: var(--background-dark);
  min-height: 100vh;
}

.main-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.game-wrapper {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(4px);
}

.betting-controls {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin: 2rem 0;
}

.betting-controls input {
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid var(--accent-color);
  border-radius: 8px;
  padding: 0.5rem 1rem;
  color: var(--text-light);
  width: 100px;
}

.bet-label {
  color: var(--accent-color);
  font-weight: bold;
}

/* Update existing card styles */
.card {
  background: var(--primary-gradient);
  color: var(--background-dark);
  font-weight: bold;
  transform-style: preserve-3d;
  transition: transform 0.3s ease;
}

.card:hover {
  transform: translateY(-5px);
}
